#lecture {
  display: flex;
  font-family: sans-serif;
  justify-content: center;
  padding-top: 5rem;
}
.lecture-container {
  padding: 2rem 6rem;
  width: 50rem;
  height: auto;
  box-shadow: 0 0 20px black;
}

.lecture-notes h2 {
  font-size: 40px;
  color: rgba(203, 140, 23, 0.89);
  padding-bottom: 3rem;
}
code {
  background-color: rgb(250, 246, 246);
  padding: 5px 5px;
}
pre {
  margin-top: 10px;
}

.lecture-notes a {
  color: rgb(89, 0, 255);
  text-shadow: 0 0 10px rgb(76, 205, 34);
  text-decoration: none;
}

@media (max-width: 50rem) {
  .lecture-container {
    width: 90vw;
    padding: 2rem 1rem;
    height: auto;
  }
  pre {
    font-size: 7px;
  }
}

.lecture-notes h3 {
  color: rgb(152, 91, 17);
  font-size: 30px;
}
.lecture-notes ul {
  padding: 1.5rem 1rem;
}
.lecture-notes li {
  padding-bottom: 5px;
}
