#footer{
    display: flex;
    flex-direction: column;
    background-color:rgb(78, 75, 75);
    /* position: static; */

    justify-content: center;
    justify-items: center;
    align-items: center;
    width: 100vw;
    height: 20vh;
    color: white;
    font-size: 1.5rem;
}
#footer i{
    margin-top: 1.5rem;
    color: white;
    font-size: 1.5rem;
    padding-inline: 1.5rem;
    cursor: pointer;
    
}
#footer i:hover{
    color: rgb(85, 232, 85);
    transform: scale(1.4);
    text-shadow: 0 0 10px rgb(51, 133, 0);
}
