.lecture-title{
    font-family: sans-serif;
}
.lecture-title hr{
color: rgb(51, 217, 51);
background-color: rgb(29, 210, 29);
height:1px;
box-shadow: 0 0 5px rgb(29, 210, 29);
margin: 3rem 0;
}
.lecture-title h2{
    font-size: 45px;
    padding-bottom: 10px;
}
.lecture-title h3{
    margin-bottom: 20px;
    color: rgba(128, 66, 0, 0.601);
}

.lecture-title a{
    display: flex;
    flex-direction: column;
    margin:10px 0  0 60px;
    text-decoration: none;
    color: black;
    font-size: 17px;
    scroll-behavior: smooth;
}
.lecture-title a:hover{
    color: rgb(25, 205, 40);
    transform: translateX(22px);
    transition: all 1s;
}