.navbar {
  background-color: black;
  height: 5rem;
  display: flex;
  align-items: center;
  /* position: fixed; */
  width: 100vw;
}
.navbar a {
  color: rgb(255, 255, 255);
  font-size: 25px;
  margin-left: 2rem;
  font-family: sans-serif;
  text-decoration: none;
}
.navbar li{
  list-style: none;
}
.navbar li a {
  color: rgb(255, 255, 255);
  font-size: 15px;
  margin-top: -5px;
  margin-left: 2rem;
  position: absolute;
  right: 3rem;
  font-family: sans-serif;
  text-decoration: none;
  cursor: pointer;
}

.navbar a:hover{
color: rgb(43, 210, 43);
transform: scale(1.1);
}
